  transition: border-color 0.3s; }
  .filter__from-to-input:hover {
    border-color: #79818C; }
    .filter__from-to-input:hover ::-webkit-input-placeholder {
      /* Chrome */
      color: #1F1F25; }
    .filter__from-to-input:hover :-ms-input-placeholder {
      /* IE 10+ */
      color: #1F1F25; }
    .filter__from-to-input:hover ::-moz-placeholder {
      /* Firefox 19+ */
      color: #1F1F25;
      opacity: 1; }
    .filter__from-to-input:hover :-moz-placeholder {
