  .filter__control-btn:active {
    background-color: #EAF1F9; }

.filter__control-btn--yellow {
  background-color: #FFD400; }
  .filter__control-btn--yellow:hover {
    background-color: #FDCA21; }
  .filter__control-btn--yellow:active {
