.circle-btn {
  width: 40px;
  height: 40px;
  padding: 0;
  border-radius: 50%; }

.result {
  margin-bottom: 150px; }
  .result.active {
    display: none; }
  .result__title {
    color: #1F1F25;
    font-size: 24px;
    font-weight: 700;
    margin-bottom: 18px; }
  .result__table {
    width: 100%;
    border-collapse: collapse; }
  .result__row {
    border-bottom: 1px solid #F1F5F9;
    width: 100%;
    padding: 18px 0;
    text-align: left;
    font-size: 16px;
    display: flex;
    align-items: center; }
  .result__row:first-child {
    font-size: 14px; }
    .result__row:first-child th {
      font-weight: 400; }
  .result__cell {
    font-weight: 400;
    max-width: 111.6px;
    width: 100%;
    display: block; }
  .result__cell:first-child, .result__price {
    font-weight: 700; }
  .result__buttons-cell {
    display: flex;
    width: 100%; }
  .result__favour {
    margin-left: 21px; }
  .result__show-item {
    margin-left: 20px;
    background: transparent !important;
    transition: transform 0.3s; }
    .result__show-item.active {
      transform: rotateX(180deg); }
  .result__favour, .result__show-item {
    background: rgba(241, 245, 249, 0.8);
    border-radius: 40px;
    width: 40px;
    height: 40px;
    border: none;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer; }

.result__row.active {
  flex-wrap: wrap;
  position: relative;
  border-bottom: none; }
  .result__row.active::after {
    content: "";
    background-color: transparent;
    height: 23px;
    width: 109%;
    position: absolute;
    bottom: 0;
    left: 0;
    box-shadow: 0px 10px 14px 0px rgba(50, 50, 50, 0.16);
    margin-left: -30px; }
  .result__row.active .result__buttons-cell {
    width: 16%; }

@keyframes slide-in-top {
  0% {
    transform: translateY(-100px);
    opacity: 0; }
  100% {
    transform: translateY(0);
    opacity: 1; } }

